import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  readonly APIUrl = 'http://localhost:50945/api';
  readonly PhotoUrl = 'http://localhost:50945/Photos/';

  constructor(private http:HttpClient) { }

  getEmpleadoList():Observable<any[]>{
    return this.http.get<any>(this.APIUrl+'/Empleados');
  }

  addEmpleado(val:any){
    return this.http.post(this.APIUrl+'/Empleados',val);
  }

  updateEmpleado(val:any){
    return this.http.put(this.APIUrl+'/Empleados',val);
  }

  deleteEmpleado(val:any){
    return this.http.delete(this.APIUrl+'/Empleados/'+val);
  }

  UploadPhoto(val:any){
    return this.http.post(this.APIUrl+'/Empleados/SaveFile',val);
  }
}
