import { Component, OnInit } from '@angular/core';
import { SharedService } from 'src/app/shared.service';

@Component({
  selector: 'app-show-emp',
  templateUrl: './show-emp.component.html',
  styleUrls: ['./show-emp.component.css']
})
export class ShowEmpComponent implements OnInit {

  constructor(private service:SharedService) { }

  EmpleadosList:any=[];

  ModalTitle:string;
  ActivateAddEditEmpComp:boolean = false;
  emp:any;

  EmpleadoIdFilter:string="";
  EmpleadoNombreFilter:string="";
  EmpleadosListWithoutFilter:any=[];

  ngOnInit(): void {
    this.refreshEmpleadosList();
  }

  addClick(){
    this.emp={
      Id:0,
      Nombre:"",
      Telefono:"",
      Direccion:"",
      Curp:""
    }
    this.ModalTitle = "Agregar Empleado";
    this.ActivateAddEditEmpComp = true;
  }
  editClick(item){
    this.emp=item;
    this.ModalTitle = "Editar Empleado";
    this.ActivateAddEditEmpComp = true;
  }
  deleteClick(item){
    if(confirm("¿Seguro que desea eliminar el registro?")){
      this.service.deleteEmpleado(item.Id).subscribe(data=>{
        alert(data.toString());
        this.refreshEmpleadosList();
      })
    }
  }
  printClick(item){
    if(confirm("¿Desea imprimir el Gafete?")){
      alert("yes");
      this.refreshEmpleadosList();
    }
  }

  closeClick(){
    this.ActivateAddEditEmpComp=false;
    this.refreshEmpleadosList();
  }

  refreshEmpleadosList(){
    this.service.getEmpleadoList().subscribe(data=>{
      this.EmpleadosList=data;
      this.EmpleadosListWithoutFilter=data;
      });
    }

    FilterFn(){
      var EmpleadoIdFilter = this.EmpleadoIdFilter;
      var EmpleadoNombreFilter = this.EmpleadoNombreFilter;
  
      this.EmpleadosList = this.EmpleadosListWithoutFilter.filter(function (el){
          return el.Id.toString().toLowerCase().includes(
            EmpleadoIdFilter.toString().trim().toLowerCase()
          )&&
          el.Nombre.toString().toLowerCase().includes(
            EmpleadoNombreFilter.toString().trim().toLowerCase()
          )
      });
    }
  
    sortResult(prop,asc){
      this.EmpleadosList = this.EmpleadosListWithoutFilter.sort(function(a,b){
        if(asc){
            return (a[prop]>b[prop])?1 : ((a[prop]<b[prop]) ?-1 :0);
        }else{
          return (b[prop]>a[prop])?1 : ((b[prop]<a[prop]) ?-1 :0);
        }
      })
    }
}
